.container {
  width: 100%;
  margin: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

tbody {
  max-height: 300px;
  overflow-y: scroll;
}

th,
td {
  padding: 10px;
  border: 1px solid #ddd;
}

th {
  background-color: #f4f4f4;
  cursor: pointer;
}

span {
  margin-left: 5px;
}

button:disabled {
  color: gray;
}

.pagination {
  display: flex;
  align-items: center;
  gap: 8px;
}
